package auth

import androidx.compose.runtime.Composable
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateListOf
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.runtime.setValue
import base.BaseViewModel
import dev.gitlive.firebase.Firebase
import dev.gitlive.firebase.database.database
import dev.icerock.moko.mvvm.viewmodel.ViewModel
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.SharingStarted
import kotlinx.coroutines.flow.StateFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.flow.combine
import kotlinx.coroutines.flow.stateIn
import kotlinx.coroutines.flow.update
import kotlinx.coroutines.launch

class LoginViewModel(
    private val authService: AuthService
) : BaseViewModel() {

    var viewModelUIState by mutableStateOf(LoginUiState())
        set

//    var ViewModelUIState by mutableStateOf(LoginUiState())

    var currentScreen = mutableStateOf(1)
    private val _uiState = MutableStateFlow(LoginUiState())
    val uiState = _uiState.asStateFlow()

    private val _emailError = MutableStateFlow(false)
    val emailError = _emailError.asStateFlow()

    private val _passwordError = MutableStateFlow(false)
    val passwordError = _passwordError.asStateFlow()

    private val _currentUser = MutableStateFlow<User?>(null)
    val currentUser = _currentUser.asStateFlow()

    private val _isProcessing = MutableStateFlow(false)
    val isProcessing = _isProcessing.asStateFlow()

    private val isButtonEnabled: StateFlow<Boolean> = combine(uiState) { states ->
        val state = states.first()
        state.email.isNotBlank() && state.password.isNotBlank()
    }.stateIn(
        viewModelScope, SharingStarted.WhileSubscribed(5000), false
    )

    init {
        launchWithCatchingException {
            authService.currentUser.collect {
                _currentUser.value = it
            }
        }

    }

    fun onEmailChange(newValue: String) {
        _uiState.update { it.copy(email = newValue) }
        //reset error
        if (newValue.isNotBlank()) _emailError.value = false
    }

    fun onPasswordChange(newValue: String) {
        _uiState.update { it.copy(password = newValue) }
        //reset error
        if (newValue.isNotBlank()) _passwordError.value = false
    }

    fun updateEmail(newEmail: String) {
        viewModelUIState = viewModelUIState.copy(email = newEmail)

    }
    fun onSignInClick() {

//        if (_uiState.value.email.isEmpty()) {
//            _emailError.value = true
//            return
//        }
//
//        if (_uiState.value.password.isEmpty()) {
//            _emailError.value = true
//            return
//        }

        launchWithCatchingException {
            _isProcessing.value = true
            //val result = authService.createUser(_uiState.value.email, _uiState.value.password)
            authService.authenticate(_uiState.value.email, _uiState.value.password) // main

//            authService.authenticate(viewModelUIState.email, viewModelUIState.password)
            _isProcessing.value = false
        }

    }


    fun onSignOut() {
        launchWithCatchingException {
            authService.signOut()
        }
    }


    private val db = Firebase.database("https://teamu-fdfab-default-rtdb.firebaseio.com/")
    val esp = mutableStateListOf<Keys>()
    fun database() {
        viewModelScope.launch {
            db.reference(currentUser.value?.email.toString().removeSuffix(".com")).valueEvents.collect { dataSnapshot ->
                val receivedList = dataSnapshot.children.map { child ->
                    val name = child.child("name").value?.toString() ?: ""
                    val status = child.child("status").value?.toString() ?: ""
                    val type = child.child("type").value?.toString() ?: ""

                    Keys(child.key.toString(), touchKey = TouchKey(name, status, type))
                }
                esp.clear()
                esp.addAll(receivedList)
            }
        }
    }

    fun updateChildren(status: String, path: String) {
        viewModelScope.launch {
            db.reference("$path/status").setValue(status)
        }
    }
}

data class LoginUiState(
    val email: String = "",
    val password: String = ""
)


data class TouchKey(
    val name: String,
    val status: String,
    val type: String
)

data class Keys(
    var key: String,
    var touchKey: TouchKey
)
