@Composable
fun CustomLayout(viewModel: LoginViewModel) {
    val screenWidth = window.innerWidth
    val isMobile = screenWidth <= 768
    Div(
        attrs = {
            style {
                backgroundColor(Color.white)
                width(100.percent)
                height(100.vh)
                display(DisplayStyle.Flex)
                flexDirection(FlexDirection.Column)
                justifyContent(JustifyContent.Center)
                alignItems(AlignItems.Center)
            }
        }
    ) {
        Div(
            attrs = {
                style {
                    //width(100.percent)
                    height(100.vh)
                    width(if (isMobile) 100.percent else 400.px)
                    display(DisplayStyle.Flex)
                    flexDirection(FlexDirection.Column)
                }
            }
        ) {
            NavHost(viewModel = viewModel, isMobile)
            BottomNavigation(viewModel = viewModel)
        }
    }
}


@Composable
fun BottomNavigation(viewModel: LoginViewModel) {
    var selectedIndex by remember { mutableStateOf(1) }

    //    
    Div(
        attrs = {
            style {
                display(DisplayStyle.Flex)
                justifyContent(JustifyContent.SpaceEvenly)
                alignItems(AlignItems.Center)
                backgroundColor(Color("#353535"))
                height(78.px) //    BottomNavigation
                //border(1.px, LineStyle.Solid, Color.lightgray)
            }
        }

    ) {
        val items = listOf(
            Pair("fa-user", "Profile"),
            Pair("fa-home", "Home"),
            Pair("fa-cog", "Setting"),
        )

        items.forEachIndexed { index, (icon, text) ->
            BottomNavItem(
                icon = icon,
                text = text,
                isHome = index == 1,
                isSelected = index == selectedIndex,
                onClick = {
                    selectedIndex = index
                    viewModel.currentScreen.value = index
                }
            )
        }
    }
}

@Composable
fun BottomNavItem(
    icon: String,
    text: String,
    isSelected: Boolean,
    isHome: Boolean = false,
    onClick: () -> Unit
) {
    Div(
        attrs = {
            style {
                flex(1)
                textAlign("center")
                cursor("pointer")
                padding(8.px)

                if (isHome) {
                    display(DisplayStyle.Flex)
                    justifyContent(JustifyContent.Center)
                    alignItems(AlignItems.Center)
                    color(if (isSelected) Color("#FFFFFF") else Color("#CCCCCC"))
                } else
                    color(if (isSelected) Color("#FF9800") else Color("#666666"))
            }
            onClick { onClick() }
        }
    ) {
        if (isHome) {
            Div(
                attrs = {
                    style {
                        width(56.px)
                        height(56.px)
                        backgroundColor(Color("#FF9800"))
                        borderRadius(50.percent)
                        display(DisplayStyle.Flex)
                        justifyContent(JustifyContent.Center)
                        alignItems(AlignItems.Center)
                        property("box-shadow", "0px 4px 6px rgba(0, 0, 0, 0.1)")
                        border(2.px, LineStyle.Solid, Color("#FF9800"))
                    }
                }
            ) {
                I(attrs = {
                    classes("fa", icon)
                    style {
                        fontSize(21.px)
                    }
                })
            }
        } else {
            I(attrs = {
                classes("fa", icon)
                style {
                    fontSize(18.px)
                    marginBottom(4.px)
                }
            })
            Br()
            Span { Text(text) }
        }
    }
}

@Composable
private fun NavHost(viewModel: LoginViewModel, isMobile: Boolean) {
    Div(
        attrs = {
            style {
                backgroundColor(Color("#575757"))
                display(DisplayStyle.Flex)
                flexDirection(FlexDirection.Column)
                flexGrow(1)
            }
        }
    ) {
        val emailError by viewModel.emailError.collectAsState()
        val passwordError by viewModel.passwordError.collectAsState()
        val isProcessing by viewModel.isProcessing.collectAsState()
        val currentUser by viewModel.currentUser.collectAsState()

        when (viewModel.currentScreen.value) {
            0 -> LoginScreenContent(
                onEmailChange = viewModel::onEmailChange,
                onPasswordChange = viewModel::onPasswordChange,
                onSignInClick = { viewModel.onSignInClick() },
                isProcessing = isProcessing,
                currentUser = currentUser,
                isError = emailError || passwordError,
                onSignOut = viewModel::onSignOut
            )

            1 -> MainScreen(viewModel, isMobile)
        }
    }
}

@Composable
fun LoginScreenContent(
    onEmailChange: (String) -> Unit,
    onPasswordChange: (String) -> Unit,
    onSignInClick: () -> Unit,
    isProcessing: Boolean,
    currentUser: User?,
    isError: Boolean,
    onSignOut: () -> Unit,
) {
    Div(attrs = {
        style {
            backgroundColor(Color("#575757"))
            padding(32.px)
            borderRadius(8.px)
            //width(100.percent)
            height(100.percent)
            //boxShadow(0.px, 4.px, 10.px, rgba(0, 0, 0, 0.1))
            display(DisplayStyle.Flex)
            flexDirection(FlexDirection.Column)
            alignItems(AlignItems.Center)
            justifyContent(JustifyContent.Center)
            textAlign("center")
        }
    }) {


        Div(attrs = { style { flex(1) } })

        if (currentUser?.isAnonymous == true) {
            StyledText(
                "Please sign in to continue",
            fontSize = 20.px,
            color = Color.white,
            marginBottom = 16.px
            )

            Input(type = InputType.Text, attrs = {
                placeholder(value = "Email")
                onInput { event -> onEmailChange(event.value) }
                style {
                    width(100.percent)
                    height(40.px)
                    borderRadius(8.px)
                    backgroundColor(Color.darkgray)
                    border(1.px, LineStyle.Solid, Color.white)
                    outline("none")
                    padding(8.px)
                    marginBottom(12.px)
                }
            })

            Input(type = InputType.Password, attrs = {
                placeholder("Password")
                onInput { event -> onPasswordChange(event.value) }
                style {
                    width(100.percent)
                    height(40.px)
                    borderRadius(8.px)
                    backgroundColor(Color.darkgray)
                    border(1.px, LineStyle.Solid, Color.white)
                    outline("none")
                    padding(8.px)
                    marginBottom(16.px)
                }
            })
        }

        if (isProcessing) {
            StyledText("Processing...", color = Color.darkgray, marginBottom = 12.px)
        } else {
            if (currentUser?.isAnonymous == true) {
                Button(attrs = {
                    onClick { onSignInClick() }
                    style {
                        width(100.percent)
                        height(45.px)
                        backgroundColor(Color.black)
                        color(Color.white)
                        borderRadius(8.px)
                        fontSize(16.px)
                        cursor("pointer")
                        marginBottom(16.px)
                    }
                }) {
                    StyledText("SIGN IN", fontSize = 16.px, color = Color.white)
                }
                Div(attrs = { style { flex(1) } })
            }
        }

        if (currentUser != null && !currentUser.isAnonymous) {
            StyledText("Login Successful", color = Color.white, marginBottom = 12.px)
            Div (attrs = {
                onClick { onSignOut() }
                style {
                    width(100.px)
                    height(35.px)
                    backgroundColor(Color.black)
                    color(Color.white)
                    borderRadius(6.px)
                    cursor("pointer")
                    marginBottom(32.px)
                    display(DisplayStyle.Flex)
                    flexDirection(FlexDirection.Column)
                    alignItems(AlignItems.Center)
                    justifyContent(JustifyContent.Center)
                    textAlign("center")
                }
            }) {
                StyledText("Log Out", color = Color.white)
            }
        }

        if (isError) {
            StyledText(
                " Invalid email or password!",
                fontSize = 14.px,
                color = Color.red,
                marginTop = 16.px
            )
        }
    }
}

@Composable
fun MainScreen(viewModel: LoginViewModel, isMobile: Boolean) {
    viewModel.database()
    val user by viewModel.currentUser.collectAsState()

    Div(
        attrs = {
            style {
                width(100.percent)
                flex(1)
                display(DisplayStyle.Flex)
                flexDirection(FlexDirection.Column)
            }
        }
    ) {
        // **Header Section**
        Div(
            attrs = {
                style {
                    backgroundColor(Color("#353535"))
                    borderRadius(0.px, 0.px, 8.px, 8.px)
                    padding(16.px)
                }
            }
        ) {
            Div(
                attrs = {
                    style {
                        display(DisplayStyle.Flex)
                        alignItems(AlignItems.Center)
                        width(100.percent)
                    }
                }
            ) {
                I(
                    attrs = {
                        classes("fa", "fa-bars")
                        style {
                            fontSize(24.px)
                            color(Color.white)
                        }
                    }
                )
                Div(attrs = { style { flex(1) } }) // Spacer

                Img(
                    src = "small_logo.png", //  
                    attrs = {
                        style {
                            width(56.px)
                            height(56.px)
                            borderRadius(50.percent)
                        }
                    }
                )
            }

            // **Title**
            Div(
                attrs = {
                    style {
                        display(DisplayStyle.Flex)
                        alignItems(AlignItems.Center)
                        marginTop(8.px)
                    }
                }
            ) {
                P(attrs = { style { color(Color.white); fontSize(12.px) } }) { Text("Welcome to.") }
                P(attrs = {
                    style {
                        color(Color("#FF9800"))
                        fontSize(20.px)
                        fontWeight("bold")
                        marginLeft(6.px)
                    }
                }) { Text("TeamU") }
            }

            P(attrs = {
                style {
                    color(Color.white)
                    fontSize(18.px)
                    fontWeight("bold")
                    marginTop(4.px)
                }
            }) { Text("Smart Home") }
        }

        // **Scrollable List Section**
        Div(
            attrs = {
                style {
                    flex(1)
                    display(DisplayStyle.Flex)
                    flexDirection(FlexDirection.Column)
                    padding(16.px)

                    //        
                    //maxHeight(60.vh) //   70%  (  )
                    overflowY("auto") //     
                }
            }
        ) {
            for (device in viewModel.esp) {
                Div(
                    attrs = {
                        style {
                            padding(16.px)
                            display(DisplayStyle.Flex)
                            flexDirection(FlexDirection.Column)
                            alignItems(AlignItems.Center)
                            justifyContent(JustifyContent.Center)
                        }
                    }
                ) {
                    Key4Touch(device, user, viewModel, isMobile)
                }
            }
        }
    }
}

@Composable
fun Key4Touch(keys: Keys, userValue: User?, viewModel: LoginViewModel, isMobile: Boolean) {
    var booleanArray by remember { mutableStateOf(keys.touchKey.status.map { it == '1' }.toBooleanArray()) }
    Div(
        attrs = {
            style {
                //padding(24.px)
                width(if (isMobile) 90.vw else 350.px)
                height(if (isMobile) 90.vw else 350.px)
                //position(Position.Relative) //    

                border(2.px, LineStyle.Solid, Color.gray)
                backgroundColor(Color("#1A1A1A"))
                borderRadius(8.px)
                display(DisplayStyle.Flex)
                flexDirection(FlexDirection.Column)
                justifyContent(JustifyContent.SpaceBetween)
                alignItems(AlignItems.Center)
            }
        }
    ) {
        // ** **
        P(
            attrs = {
                style {
                    fontWeight("bold")
                    fontSize(13.px)
                    color(Color.white)
                    marginTop(8.px)
                }
            }
        ) { Text(keys.touchKey.name) }

        // ****
        Div(
            attrs = {
                style {
                    flex(1)
                    width(100.percent)
                    display(DisplayStyle.Flex)
                    flexDirection(FlexDirection.Column)
                    justifyContent(JustifyContent.SpaceEvenly)
                    alignItems(AlignItems.Center)
                }
            }
        ) {
            repeat(2) { rowIndex ->
                Div(
                    attrs = {
                        style {
                            display(DisplayStyle.Flex)
                            justifyContent(JustifyContent.SpaceEvenly)
                            width(100.percent)
                        }
                    }
                ) {
                    repeat(2) { colIndex ->
                        val index = rowIndex * 2 + colIndex
                        Div(
                            attrs = {
                                style {
                                    display(DisplayStyle.Flex)
                                    justifyContent(JustifyContent.SpaceEvenly)
                                    width(100.percent)

                                }
                            }
                        ) {
                            KeyBox(booleanArray[index]) {
                                booleanArray =
                                    booleanArray.copyOf().apply { this[index] = !this[index] }
                                viewModel.updateChildren(
                                    booleanArray.joinToString("") { if (it) "1" else "0" },
                                    "${
                                        userValue?.email.toString().removeSuffix(".com")
                                    }/${keys.key}"
                                )
                            }
                        }
                    }
                }
            }
        }

        // ** **
        P(
            attrs = {
                style {
                    fontWeight("bold")
                    fontSize(13.px)
                    color(Color.white)
                    alignSelf(AlignSelf.FlexStart)
                    marginLeft(12.px)
                    marginBottom(6.px)
                }
            }
        ) { Text("TeamU") }
    }
}

@Composable
fun KeyBox(isTouched: Boolean, onClick: () -> Unit) {
    Div(
        attrs = {
            style {
                width(48.px)
                height(48.px)
                borderRadius(8.px)
                border(2.px, LineStyle.Solid, if (isTouched) Color("#32A5E0") else Color.white)
                padding(4.px)
                cursor("pointer")
            }
            onClick { onClick() }
        }
    )
}












@Composable
fun StyledText(
    message: String,
    color: CSSColorValue = Color.white,
    fontSize: CSSSizeValue<out CSSUnit> = 16.px,
    fontWeight: String = "bold",
    backgroundColor: CSSColorValue = Color.transparent,
    padding: CSSSizeValue<out CSSUnit> = 8.px,
    marginBottom: CSSSizeValue<out CSSUnit> = 0.px,
    marginTop: CSSSizeValue<out CSSUnit> = 0.px
) {
    Span(attrs = {
        style {
            color(color)
            fontSize(fontSize)
            fontWeight(fontWeight)
            backgroundColor(backgroundColor)
            padding(padding)
            marginBottom(marginBottom)
            marginTop(marginTop)
            borderRadius(6.px)
        }
    }) {
        Text(message)
    }
}


